*, :before, :after {
  box-sizing: border-box;
}

html {
  tab-size: 4;
  -webkit-text-size-adjust: 100%;
  line-height: 1.15;
}

body {
  margin: 0;
  font-family: system-ui, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, -apple-system, Segoe UI, Roboto, Helvetica, Arial, sans-serif, Apple Color Emoji, Segoe UI Emoji;
}

hr {
  height: 0;
  color: inherit;
}

abbr[title] {
  -webkit-text-decoration: underline dotted;
  text-decoration: underline dotted;
}

b, strong {
  font-weight: bolder;
}

code, kbd, samp, pre {
  font-family: ui-monospace, SFMono-Regular, Consolas, Liberation Mono, Menlo, monospace;
  font-size: 1em;
}

small {
  font-size: 80%;
}

sub, sup {
  vertical-align: baseline;
  font-size: 75%;
  line-height: 0;
  position: relative;
}

sub {
  bottom: -.25em;
}

sup {
  top: -.5em;
}

table {
  text-indent: 0;
  border-color: inherit;
}

button, input, optgroup, select, textarea {
  margin: 0;
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
}

button, select {
  text-transform: none;
}

button, [type="button"], [type="reset"], [type="submit"] {
  -webkit-appearance: button;
}

::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

:-moz-focusring {
  outline: 1px dotted ButtonText;
}

:-moz-ui-invalid {
  box-shadow: none;
}

legend {
  padding: 0;
}

progress {
  vertical-align: baseline;
}

::-webkit-inner-spin-button, ::-webkit-outer-spin-button {
  height: auto;
}

[type="search"] {
  -webkit-appearance: textfield;
  outline-offset: -2px;
}

::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}

summary {
  display: list-item;
}

:root {
  --rich-black-fogra-29: #11141d;
  --rich-black-fogra-39: #0a0f0f;
  --raisin-black: #21232b;
  --eerie-black: #171d21;
  --light-gray: #cecaca;
  --gunmetal-1: #2e303e;
  --gunmetal-2: #242c38;
  --gainsboro: #e3dede;
  --citrine: #e2d703;
  --xiketic: #1d1a28;
  --gray-x: #bdbdbd;
  --white: #fff;
  --black: #000;
  --jet: #333;
  --ff-poppins: "Poppins", sans-serif;
  --fs-1: 36px;
  --fs-2: 32px;
  --fs-3: 30px;
  --fs-4: 24px;
  --fs-5: 20px;
  --fs-6: 18px;
  --fs-7: 16px;
  --fs-8: 15px;
  --fs-9: 14px;
  --fs-10: 13px;
  --fs-11: 12px;
  --fs-12: 11px;
  --fw-500: 500;
  --fw-700: 700;
  --transition-1: .15s ease;
  --transition-2: .25s ease-in;
  --section-padding: 100px;
}

*, :before, :after {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

li {
  list-style: none;
}

a {
  text-decoration: none;
}

a, img, span, input, button, ion-icon {
  display: block;
}

input {
  font: inherit;
  width: 100%;
  border: none;
}

select, button {
  font: inherit;
  cursor: pointer;
  background: none;
  border: none;
}

html {
  font-family: var(--ff-poppins);
  scroll-behavior: smooth;
}

body {
  background: var(--eerie-black);
}

body.active {
  overflow: hidden;
}

.container {
  padding-left: 15px;
  padding-right: 15px;
}

.h1, .h2, .h3 {
  color: var(--white);
  line-height: 1.2;
}

.h1 {
  font-size: var(--fs-1);
}

.h2 {
  font-size: var(--fs-2);
}

.h3 {
  font-size: var(--fs-6);
}

.section-subtitle {
  color: var(--citrine);
  font-size: var(--fs-11);
  font-weight: var(--fw-500);
  text-transform: uppercase;
  letter-spacing: 1px;
  text-align: center;
  margin-bottom: 10px;
}

.section-title {
  text-align: center;
}

.btn {
  color: var(--white);
  font-size: var(--fs-11);
  font-weight: var(--fw-700);
  text-transform: uppercase;
  letter-spacing: 2px;
  border: 2px solid var(--citrine);
  transition: var(--transition-1);
  border-radius: 50px;
  align-items: center;
  gap: 10px;
  padding: 16px 30px;
  display: flex;
}

.btn > ion-icon {
  font-size: 18px;
}

.btn-primary {
  background: var(--rich-black-fogra-29);
}

.btn-primary:-webkit-any(:hover, :focus) {
  background: var(--citrine);
  color: var(--xiketic);
}

.btn-primary:is(:hover, :focus) {
  background: var(--citrine);
  color: var(--xiketic);
}

time {
  gap: 3px;
  display: flex;
}

@media (min-width: 550px) {
  :root {
    --fs-1: 42px;
  }

  .container {
    max-width: 540px;
    margin-left: auto;
    margin-right: auto;
  }

  .movies-list {
    grid-template-columns: 1fr 1fr;
    gap: 60px 30px;
  }
}

@media (min-width: 768px) {
  :root {
    --fs-2: 36px;
  }

  .container {
    max-width: 720px;
  }
}

@media (min-width: 992px) {
  .container {
    max-width: 960px;
  }

  .movies-list {
    grid-template-columns: repeat(3, 1fr);
  }
}

@media (min-width: 1200px) {
  :root {
    --fs-1: 60px;
    --fs-4: 26px;
  }

  .container {
    max-width: 1320px;
  }

  .movies-list {
    grid-template-columns: repeat(4, 1fr);
  }
}

.go-top {
  background: var(--citrine);
  color: var(--eerie-black);
  width: 50px;
  height: 50px;
  opacity: 0;
  visibility: hidden;
  transition: var(--transition-2);
  z-index: 2;
  border-radius: 50%;
  align-items: center;
  justify-items: center;
  display: grid;
  position: fixed;
  bottom: 20px;
  right: 20px;
  box-shadow: 0 1px 3px rgba(0, 0, 0, .5);
}

.go-top.active {
  opacity: 1;
  visibility: visible;
}

.header {
  width: 100%;
  transition: var(--transition-2);
  z-index: 4;
  padding-top: 25px;
  padding-bottom: 25px;
  position: fixed;
  top: 0;
  left: 0;
}

.header.active {
  background: var(--eerie-black);
  padding-top: 20px;
  padding-bottom: 20px;
}

.header .container {
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.header-actions {
  display: none;
}

.menu-open-btn {
  color: var(--white);
  font-size: 40px;
}

.logo {
  justify-content: center;
  align-items: center;
  gap: 20px;
  display: flex;
}

.logo p {
  letter-spacing: .15em;
  color: var(--white);
  font-size: 24px;
  font-weight: 700;
}

#logo-img {
  fill: red;
  width: 52px;
  height: 52px;
}

.navbar {
  background: var(--eerie-black);
  width: 100%;
  max-width: 300px;
  height: 100%;
  visibility: hidden;
  z-index: 3;
  transition: all .15s ease-in;
  position: fixed;
  top: 0;
  right: -300px;
  box-shadow: -1px 0 3px rgba(0, 0, 0, .2);
}

.navbar.active {
  visibility: visible;
  transition: all .25s ease-out;
  right: 0;
}

.navbar-top {
  justify-content: space-between;
  align-items: center;
  padding: 30px 25px;
  display: flex;
}

.menu-close-btn {
  color: var(--white);
  padding: 5px;
  font-size: 25px;
}

.menu-close-btn ion-icon {
  --ionicon-stroke-width: 80px;
}

.navbar-list {
  border-top: 1px solid rgba(255, 255, 255, .1);
  margin-bottom: 30px;
}

.navbar-link {
  color: var(--white);
  font-size: var(--fs-8);
  font-weight: var(--fw-500);
  transition: var(--transition-1);
  border-bottom: 1px solid rgba(255, 255, 255, .1);
  padding: 10px 25px;
}

.navbar-link:-webkit-any(:hover, :focus) {
  color: var(--citrine);
}

.navbar-link:is(:hover, :focus) {
  color: var(--citrine);
}

.navbar-social-list {
  justify-content: center;
  align-items: center;
  gap: 20px;
  display: flex;
}

.navbar-social-link {
  color: var(--white);
  transition: var(--transition-1);
  font-size: 20px;
}

.navbar-social-link:-webkit-any(:hover, :focus) {
  color: var(--citrine);
}

.navbar-social-link:is(:hover, :focus) {
  color: var(--citrine);
}

.overlay {
  opacity: 0;
  pointer-events: none;
  z-index: 1;
  transition: var(--transition-2);
  background: rgba(23, 29, 33, .8);
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}

.overlay.active {
  opacity: 1;
  pointer-events: all;
}

@media (min-width: 768px) {
  .header .container {
    gap: 30px;
  }

  .header-actions {
    align-items: center;
    gap: 35px;
    margin-left: auto;
    display: flex;
  }

  .header-actions .btn-primary {
    display: none;
  }

  .search-btn {
    color: var(--white);
    position: relative;
  }

  .search-btn ion-icon {
    --ionicon-stroke-width: 80px;
  }

  .search-btn:after {
    content: "";
    width: 2px;
    height: 14px;
    background: rgba(255, 255, 255, .1);
    position: absolute;
    top: 1px;
    right: -20px;
  }

  .logo p {
    letter-spacing: .15em;
    color: var(--white);
    font-size: 28px;
    font-weight: 700;
  }

  #logo-img {
    fill: red;
    width: 58px;
    height: 58px;
  }

  .lang-wrapper {
    align-items: center;
    gap: 5px;
    display: flex;
  }

  .lang-wrapper label {
    color: var(--citrine);
    font-size: 20px;
  }

  .lang-wrapper select {
    color: var(--white);
    font-size: var(--fs-9);
    font-weight: var(--fw-700);
  }

  .lang-wrapper option {
    color: var(--black);
  }
}

@media (min-width: 992px) {
  .header.active {
    padding-top: 5px;
    padding-bottom: 5px;
  }

  .menu-open-btn, .navbar-top, .navbar-social-list {
    display: none;
  }

  .logo p {
    letter-spacing: .15em;
    color: var(--white);
    font-size: 30px;
    font-weight: 700;
  }

  #logo-img {
    fill: red;
    width: 62px;
    height: 62px;
  }

  .navbar {
    all: unset;
    margin-left: auto;
  }

  .header-actions {
    order: 1;
    margin-left: 0;
  }

  .navbar-list {
    all: unset;
    align-items: center;
    gap: 10px;
    display: flex;
  }

  .navbar-link {
    font-size: var(--fs-9);
    font-weight: var(--fw-700);
    text-transform: uppercase;
    border-bottom: none;
    padding: 30px 15px;
  }

  .overlay {
    display: none;
  }
}

@media (min-width: 1200px) {
  .navbar {
    margin-left: auto;
    margin-right: auto;
  }

  .logo p {
    letter-spacing: .15em;
    color: var(--white);
    font-size: 34px;
    font-weight: 700;
  }

  #logo-img {
    fill: red;
    width: 66px;
    height: 66px;
  }

  .header-actions .btn-primary {
    --fs-11: 14px;
    letter-spacing: 1px;
    padding: 10px 35px;
    display: block;
  }
}

.hero {
  min-height: 750px;
  height: 100vh;
  max-height: 1000px;
  padding-block: var(--section-padding);
  background: url("hero-bg.63b03b20.jpg") center / cover no-repeat;
  justify-content: flex-start;
  align-items: center;
  display: flex;
}

.hero-content {
  margin-top: 60px;
}

.hero-subtitle {
  color: var(--citrine);
  font-size: var(--fs-4);
  font-weight: var(--fw-700);
  margin-bottom: 10px;
}

.hero-title {
  margin-bottom: 30px;
}

.hero-title > strong {
  color: var(--citrine);
}

.meta-wrapper {
  flex-wrap: wrap;
  justify-content: flex-start;
  align-items: center;
  gap: 15px 25px;
  margin-bottom: 50px;
  display: flex;
}

.badge-wrapper, .ganre-wrapper, .date-time {
  flex-wrap: wrap;
  align-items: center;
  gap: 5px 10px;
  display: flex;
}

.ganre-wrapper > a {
  color: var(--gainsboro);
  font-size: var(--fs-9);
  font-weight: var(--fw-500);
  transition: var(--transition-1);
}

.ganre-wrapper > a:-webkit-any(:hover, :focus) {
  color: var(--citrine);
}

.ganre-wrapper > a:is(:hover, :focus) {
  color: var(--citrine);
}

.date-time {
  gap: 15px;
}

.date-time > div {
  color: var(--gainsboro);
  font-size: var(--fs-9);
  font-weight: var(--fw-500);
  align-items: center;
  gap: 5px;
  display: flex;
}

.date-time ion-icon {
  --ionicon-stroke-width: 50px;
  color: var(--citrine);
}

.badge {
  color: var(--white);
  font-size: var(--fs-12);
  font-weight: var(--fw-700);
  border: 2px solid rgba(0, 0, 0, 0);
  padding: 2px 10px;
}

.badge-fill {
  background: var(--white);
  color: var(--raisin-black);
}

.badge-outline {
  border-color: var(--white);
}

@media (min-width: 550px) {
  .hero {
    min-height: unset;
  }

  .hero-content {
    margin-top: 20px;
  }
}

@media (min-width: 768px) {
  .hero-content {
    margin-top: 90px;
  }
}

@media (min-width: 992px) {
  .hero {
    background-position: 100%;
  }

  .hero .container {
    width: 950px;
  }

  .hero-content {
    max-width: 600px;
    margin-top: 100px;
  }
}

@media (min-width: 1200px) {
  .hero .container {
    width: 1320px;
  }
}

.released {
  padding-block: var(--section-padding);
  background: url("released-bg.87d707f8.png") center / cover no-repeat;
}

.released .flex-wrapper {
  margin-bottom: 50px;
}

.released .section-title {
  margin-bottom: 30px;
}

.filter-list {
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  gap: 10px;
  display: flex;
}

.released .filter-btn {
  color: var(--white);
  background: var(--raisin-black);
  font-size: var(--fs-11);
  font-weight: var(--fw-500);
  border: 2px solid var(--gunmetal-1);
  border-radius: 50px;
  padding: 12px 26px;
}

.released .filter-btn:focus {
  border-color: var(--citrine);
}

.released .movies-list > li {
  min-width: 100%;
  scroll-snap-align: start;
}

.movies-list {
  gap: 50px;
  display: grid;
}

.movie-card {
  height: 100%;
  flex-direction: column;
  justify-content: space-between;
  display: flex;
}

.movie-card .card-banner {
  background: var(--gunmetal-1);
  aspect-ratio: 2 / 3;
  transition: var(--transition-1);
  border-radius: 6px;
  margin-bottom: 20px;
  position: relative;
  overflow: hidden;
}

.movie-card .card-banner:after {
  content: "";
  transition: var(--transition-1);
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}

.movie-card .card-banner:hover {
  box-shadow: 0 1px 2px rgba(0, 0, 0, .5);
}

.movie-card .card-banner:hover:after {
  background: rgba(255, 255, 255, .05);
}

.movie-card .card-banner img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.movie-card .title-wrapper {
  justify-content: space-between;
  align-items: center;
  gap: 20px;
  margin-bottom: 10px;
  display: flex;
}

.movie-card .card-title {
  color: var(--white);
  font-size: var(--fs-7);
  transition: var(--transition-1);
}

.movie-card .card-title:-webkit-any(:hover, :focus) {
  color: var(--citrine);
}

.movie-card .card-title:is(:hover, :focus) {
  color: var(--citrine);
}

.movie-card .title-wrapper time {
  color: var(--citrine);
  font-size: var(--fs-9);
  font-weight: var(--fw-500);
}

.movie-card .card-meta {
  justify-content: flex-start;
  align-items: center;
  gap: 15px;
  display: flex;
}

.movie-card .badge {
  color: var(--citrine);
}

.movie-card .duration {
  margin-left: auto;
}

.movie-card :-webkit-any(.duration, .rating) {
  color: var(--gainsboro);
  font-size: var(--fs-11);
  font-weight: var(--fw-500);
  align-items: center;
  gap: 5px;
  display: flex;
}

.movie-card :is(.duration, .rating) {
  color: var(--gainsboro);
  font-size: var(--fs-11);
  font-weight: var(--fw-500);
  align-items: center;
  gap: 5px;
  display: flex;
}

.movie-card :is(.duration, .rating) ion-icon {
  --ionicon-stroke-width: 50px;
  color: var(--citrine);
  font-size: 13px;
}

@media (min-width: 550px) {
  .released .movies-list > li {
    min-width: calc(50% - 15px);
  }

  .movies-list {
    grid-template-columns: 1fr 1fr;
    gap: 60px 30px;
  }
}

@media (min-width: 992px) {
  .released .flex-wrapper {
    justify-content: space-between;
    align-items: center;
    display: flex;
  }

  .released :-webkit-any(.section-subtitle, .section-title) {
    text-align: left;
  }

  .released :is(.section-subtitle, .section-title) {
    text-align: left;
  }

  .released .section-title {
    margin-bottom: 0;
  }

  .released .movies-list > li {
    min-width: calc(33.33% - 20px);
  }

  .movies-list {
    grid-template-columns: repeat(3, 1fr);
  }
}

@media (min-width: 1200px) {
  .released .movies-list > li {
    min-width: calc(25% - 22.5px);
  }

  .movies-list {
    grid-template-columns: repeat(4, 1fr);
  }
}

.service {
  padding-block: var(--section-padding);
  background: url("service-bg.08437bb4.jpg") center / cover no-repeat;
}

.service-banner {
  max-width: max-content;
  margin-bottom: 50px;
  position: relative;
}

.service-banner img {
  width: 100%;
}

.service-btn {
  background: var(--citrine);
  color: var(--rich-black-fogra-29);
  font-size: var(--fs-11);
  text-transform: uppercase;
  font-weight: var(--fw-500);
  letter-spacing: 5px;
  transform-origin: 100% 100%;
  border-radius: 6px;
  align-items: center;
  gap: 5px;
  padding: 28px 35px;
  display: flex;
  position: absolute;
  bottom: 0;
  right: 86px;
  transform: rotate(.25turn);
}

.service-btn ion-icon {
  font-size: 30px;
  transform: rotate(-.25turn);
}

.service-subtitle {
  color: var(--gainsboro);
  font-size: var(--fs-11);
  font-weight: var(--fw-500);
  text-transform: uppercase;
  letter-spacing: 1px;
  margin-bottom: 10px;
  padding-left: 50px;
  position: relative;
}

.service-subtitle:before {
  content: "";
  width: 40px;
  height: 3px;
  background: var(--citrine);
  position: absolute;
  top: 7px;
  left: 0;
}

.service-title {
  margin-bottom: 20px;
}

.service-text, .service-card .card-text {
  color: var(--gray-x);
  font-size: var(--fs-9);
  font-weight: var(--fw-500);
  line-height: 1.8;
}

.service-text {
  margin-bottom: 40px;
}

.service-list > li:first-child {
  border-bottom: 1px dashed rgba(255, 255, 255, .1);
  margin-bottom: 30px;
  padding-bottom: 20px;
}

.service-card .card-icon {
  color: var(--white);
  width: 85px;
  height: 85px;
  outline: 1px dashed var(--citrine);
  outline-offset: 5px;
  transition: var(--transition-2);
  border-radius: 50%;
  align-items: center;
  justify-items: center;
  margin: 5px 5px 20px;
  font-size: 40px;
  display: grid;
}

.service-card:hover .card-icon {
  background: var(--citrine);
}

.service-card .card-title {
  margin-bottom: 10px;
}

@media (min-width: 550px) {
  .service-list > li:first-child {
    padding-bottom: 30px;
  }

  .service-card {
    align-items: center;
    gap: 20px;
    display: flex;
  }

  .service-card .card-icon {
    margin-bottom: 0;
  }

  .service-card .card-content {
    width: calc(100% - 115px);
  }
}

@media (min-width: 768px) {
  .service-btn {
    padding: 40px 50px;
    right: -20px;
  }

  .service-content {
    padding-left: 40px;
    padding-right: 40px;
  }
}

@media (min-width: 992px) {
  .service .container {
    justify-content: space-between;
    align-items: center;
