.switch {
  width: 45px;
  height: 24px;
  margin-left: 48%;
  display: inline-block;
  position: relative;
}

.switch input {
  opacity: 0;
  width: 0;
  height: 0;
}

.slider {
  cursor: pointer;
  background-color: #ccc;
  border-radius: 34px;
  transition: all .5s;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}

.slider:before {
  content: "";
  height: 18px;
  width: 18px;
  background-color: var(--gunmetal-1);
  border-radius: 50%;
  transition: all .5s;
  position: absolute;
  bottom: 3px;
  left: 4px;
}

.switch input:checked + .slider {
  background-color: var(--citrine);
}

.switch input:checked + .slider:before {
  transform: translateX(18px);
}



